.sidebar {
  background-color: #fff;
  font-style: normal;
  font-size: 14px;
  color: #415A6C;

}

.sidebar-header{
    display: flex;
    border: 1px solid;
    border-radius:5px 5px ;
    padding: 8px 12px 8px 12px;
    background-color:#466072 ;
    color: #fff;
    align-items: center;
    width: auto;
    margin-inline: 15px;
    gap: 8px;
    cursor: pointer;
    
}

.qube-icon{
    height: 14px;
    margin-top: 1px;
}

.S-h2{
    display: flex;
    border-radius:5px 5px ;
    padding: 8px 12px 8px 12px;
    background-color:#E7E9EE ;
    margin-top: 7%;
    align-items: center;
    gap: 8px;
    cursor: pointer;
    width: auto;
    margin-inline: 15px;

}

.play-icon{
    height: 14px;
}

.sidebar-header2{
    display: flex;
    border-radius:5px 5px 0px 0px;
    background-color:#7E919F;
    color: #E7E9EE;
    padding: 8px 12px 8px 12px;
    align-items: center;
    cursor: pointer;
    font-style: normal;
    /* font-size: 88px; */
    width: auto;
    margin-inline: 15px;
    margin-top: 7%;
    gap: 6px;


}

.down-icon{
    height: 16px;
    /* margin-left: 1px; */
    /* padding: 1%; */
}

.S-h21{
    padding: 8px 12px 8px 12px;
    background-color:#E7E9EE ;
    border-bottom: 1px solid #B7C3CA;
    font-style: normal;
    width: auto;
    margin-inline: 15px;
    /* gap: 8px; */

}

.S-h22{
    padding: 8px 12px 8px 12px;
    background-color:#E7E9EE ;
    border-radius: 0px 0px 5px 5px;
    box-shadow: 0px 2px 0px 0px #B7C3CA;
    width: auto;
    margin-inline: 15px;
    display: flex;
    flex-direction: row;
    /* object-fit: co; */
}

.dotted-line{
    padding: 8px 12px 8px 12px;
    border-bottom: 1px dashed #B7C3CA;
    width: auto;
    margin-inline: 15px;
    /* margin-top: 1%; */
}

.alert-icon{
    height: 13px;
    margin-left: 2px;
    /* width: 13px; */
    /* align-items: center; */
}
.logout-icon{
/* width: ; */
height: 20px;   
margin-top: 1px;
}

